body {
  background-color: green;
  margin: 0;
  padding: 0;
}

:root {
  --main-bg-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.navbar {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.links {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.navbar > .nav-list {
  margin-right: 20px;
}

.navbar > .nav-list:last-child {
  margin: 0;
}

.links:hover {
  color: var(--main-bg-color);
  text-decoration: none;
}

.is-active {
  color: var(--main-bg-color);
  position: relative;
}

.is-active:after {
  background: var(--main-bg-color);
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

